<template>
  <div id="app">
    <!-- the header part  -->
    <div class="header">
      <div class="container">
        <Header :show="show" />
      </div>
    </div>
    <!-- the content part -->
    <div class="content">
      <div class="container">
        <div class="main">
          <router-view />
        </div>
        <div  class="aside">
          <tags-cloud />
          <recently-hot />
          <NewComment />
          <Blogroll />
        </div>
      </div>
    </div>
    <!-- the footer part -->
    <footer class="footer">
      <Footer />
    </footer>
  </div>
</template>

<script>
import Header from '../components/Header.vue';
import Footer from '../components/Footer.vue';
import TagsCloud from '../components/aside/TagsCloud.vue';
import RecentlyHot from '../components/aside/RecentlyHot.vue';
import NewComment from '../components/aside/NewComment.vue';
import Blogroll from '../components/aside/Blogroll.vue';

export default {
  data() {
    return {
      show: true,
    };
  },
  components: {
    Header,
    Footer,
    TagsCloud,
    RecentlyHot,
    NewComment,
    Blogroll,
  },
  methods: {
    resize() {
      const cWidth = document.documentElement.clientWidth;
      // console.log('Home检测到resize事件!', cWidth);
      if (cWidth <= 768) {
        this.show = false;
      } else {
        this.show = true;
      }
    },
  },
  created() {
    window.onresize = this.resize;
  },
  destroyed() {
    // window.onresize = null;
  },
};
</script>

