const axios = require('axios');

/**
 * 获得文章列表
 * @param {*} page
 * @param {*} pageSize
 */
async function getArticle(page, pageSize, arg) {
  const result = await axios.get(`/api/blog?page=${page}&pageSize=${pageSize}&arg=${JSON.stringify(arg)}`)
    .then(res => res.data.data)
    .catch(error => console.error('Error:', error));
  return result;
}
/** 获得每日一句 */
async function getEveryDay() {
  const result = await axios.get('/api/everyDay')
    .then(res => res.data)
    .catch(error => console.error('Error:', error));
  return result;
}
/**
 * 获得文章
 * @param {*} id
 */
async function getArt(id) {
  const result = await axios.get(`/api/blog/${id}`)
    .then(res => res.data)
    .catch(error => console.error('Error:', error));
  return result;
}
/**
 * 发表评论
 * @param {*} commentObj
 */
async function sendComment(commentObj) {
  console.log('sendComment', commentObj);
  const result = await axios.put('/api/comment', commentObj)
    .then(res => res.data)
    .catch(error => console.error('Error:', error));
  return result;
}
/**
 * 获得评论
 * @param {*} bid
 * -1为留言评论
 */
async function getComment(bid) {
  const result = await axios.get(`/api/comment?bid=${bid}`)
    .then(res => res.data)
    .catch(error => console.error('Error:', error));
  return result;
}

/**
 * 获得随机排序tag
 * @param {*} page
 * @param {*} pageSize
 */
async function getRandomTags() { // random
  const result = await axios.get('/api/tag')
    .then(res => res.data)
    .catch(error => console.error('Error:', error));
  return result;
}
/**
 * 获得所有tag
 */
async function getAllTag() {
  const result = await axios.get('/api/tag/all')
    .then(res => res.data)
    .catch(error => console.error('Error:', error));
  return result;
}
/**
 * 获得最热文章列表
 * @param {Number} size
 */
async function getHotBlogs(size) {
  const result = await axios.get(`/api/blog/hot?size=${size}`)
    .then(res => res.data)
    .catch(error => console.error('Error:', error));
  return result;
}
/** */
async function getLatestComments(size) {
  const result = await axios.get(`/api/comment/latestComments?size=${size}`)
    .then(res => res.data)
    .catch(error => console.error('Error:', error));
  return result;
}
