<template>
  <div class="leftbar new-comment" id="new-comment">
    <div class="top">最新评论</div>
    <ul>
      <li v-for="item in latestCommentsList" :key="item.id">
        <div class="comment-fisrt">
          <span class="comment-author">{{item.user_name}}</span>
          <span class="comment-right">{{item.utime}}</span>
        </div>
        <div class="comment-second">
          <a @click="tz(item.blog_id , item.id)">{{item.comments}}</a>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import { getLatestComments } from '../../api';


export default {
  data() {
    return {
      latestCommentsList: Array,
    };
  },
  computed: {
    ...mapState(['artId']),
  },
  methods: {
    /** 跳转 */
    tz(bid, cid) {
      // console.log('new', cid);
      if (bid === -1) {
        const currentPath = this.$router.history.current.path;
        if (currentPath.indexOf('message') < 0) {
          this.$router.push('/home/message');
        }
      } else if (this.artId !== bid) {
        this.$store.commit('setArtId', { id: bid });
        this.$router.push(`/home/art/${bid}`);
      }
      this.$store.commit('setCId', { cid });
    },
  },
  async created() {
    const result = await getLatestComments(6);
    if (+result.code !== 0) {
      return;
    }
    this.latestCommentsList = result.data;
  },
};
</script>

<style lang="scss" scoped>
.new-comment {
  width: 100%;
  background-color: #444;
  border-radius: 5px;
  margin-bottom: 20px;
  .top {
    height: 40px;
    line-height: 40px;
    background-color: #191818;
    color: #fff;
    padding: 0 15px;
    border-radius: 5px;
  }
  ul {
    /* display: inline-block; */
    height: 100%;
    li {
      height: 40px;
      line-height: 20px;
      border-bottom: 1px solid #585858;
      padding: 6px 15px;
      color: #eee;
      font-size: 14px;
      font-weight: 600;
      &:last-child {
        border-bottom: none;
      }
      a {
        color: #777;
        font-size: 14px;
        font-weight: 500;
        &:hover {
          cursor: pointer;
          color: #337ab7;
        }
      }
      .comment-fisrt {
        position: relative;
        height: 20px;
      }
      .comment-author {
        position: absolute;
        /* display: inline-block; */
        /* float: left; */
      }
      .comment-right {
        position: absolute;
        display: inline-block;
        right: 0;
      }
      .comment-second {
        height: 20px;
      }
    }
  }
}
</style>
