<template>
  <a @click="tz(tagData.id)"
    :key="tagData.tag"
    :style="{ color:'#fff', backgroundColor:randomColor }"
  >{{tagData.tag}}</a>
</template>

<script>
import { mapState } from 'vuex';

export default {
  props: {
    tagData: Object,
  },
  computed: {
    ...mapState([]),
    randomColor() {
      let red = (Math.random() * 255 + 20);
      let green = Math.random() * 255 + 20;
      let blue = Math.random() * 255 + 20;
      red = red > 230 ? red - 60 : red;
      green = green > 230 ? green - 60 : green;
      blue = blue > 230 ? blue - 60 : blue;
      const result = `rgba(${red},${green},${blue},1)`;
      return result;
    },
    randomFontSize() {
      const size = Math.random() * 15 + 10;
      return `${size}px`;
    },

  },
  methods: {
    tz(tid) {
      this.$store.commit('setSearchVal', { cval: null });
      this.$store.commit('setTagId', { tid });
      const currentPath = this.$router.history.current.path;
      if (currentPath.indexOf('main') < 0) {
        this.$router.push('/home/main');
      }
    },
  },
};
</script>

<style lang="scss" scoped>
a{
    display: inline-block;
    margin: 0 10px 10px 0;
    padding: 5px 10px;
    border-radius: 15px;
    text-decoration: none;
    &:hover{
        text-decoration:underline;
    }
}

</style>
