<template>
  <div class="leftbar recently-hot" id="recently-hot">
    <div class="top">热门文章</div>
    <ul>
            <li v-for="item in hotList" :key="item.id">
        <a  @click="tz(item.id)">{{item.title}}
        <!--
         {{ $store.state.artId }}
           {{ $store.getters.artId }}
             {{ artId }}-->
         </a>
      </li>
    </ul>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import { getHotBlogs } from '../../api';
import { smoothScroll } from '../../utile';

export default {
  data() {
    return {
      hotList: [],
    };
  },
  computed: {
    ...mapState(['artId']),
  },
  methods: {
    /** 跳转 */
    tz(id) {
      if (this.artId !== id) {
        this.$store.commit('setArtId', { id });
        this.$router.push(`/home/art/${id}`);
      }
      smoothScroll();
      // const currentPath = this.$router.history.current.path;
      // if (currentPath.indexOf('art') < 0) {
      // this.$router.push(`/home/art/${id}`);
      // }
      // console.log(this.$router.history.current.path);
    },
  },
  async created() {
    const result = await getHotBlogs(10);
    if (+result.code !== 0) {
      return;
    }
    this.hotList = JSON.parse(result.data);
  },
};
</script>

<style lang="scss" scoped>
.recently-hot {
  width: 100%;
  // height: 450px;
  background-color: #444;
  border-radius: 5px;
  margin-bottom: 20px;
  .top {
    height: 40px;
    line-height: 40px;
    background-color: #191818;
    color: #fff;
    padding: 0 15px;
    border-radius: 5px;
  }
  ul {
    /* display: inline-block; */
    height: 100%;
    li {
      display: block;
      height: 40px;
      line-height: 40px;
      padding-left: 10px;
      box-sizing: border-box;
      border-bottom: 1px solid #555;
      a{
        display: block;
        max-width: 300px;
         height: 40px;
        line-height: 40px;
        color: #eeeeee;
        text-decoration: none;
         overflow: hidden;
         text-overflow: ellipsis;
        //  word-wrap:break-word;
        white-space: nowrap;
        &:hover{
          cursor: pointer;
          color: #337ab7;
        }
      }
      &:last-child{
         border-bottom: none;
      }
    }
  }
}
</style>
