import Vue from 'vue';
import Router from 'vue-router';
import Home from './views/Home.vue';
import store from './store/index';

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [

    {
      path: '/',
      redirect: { name: 'home' },
    },
    {
      path: '/home',
      name: 'home',
      component: Home,
      redirect: { name: 'main' },
      children: [
        {
          path: 'art/:id',
          name: 'art/:id',
          component: () => import(/* webpackChunkName: "about" */ './views/blogDetile.vue'),
        },
        {
          path: 'main',
          name: 'main',
          component: () => import(/* webpackChunkName: "about" */ './views/Main.vue'),
        },
        {
          path: 'about',
          name: 'about',
          component: () => import(/* webpackChunkName: "about" */ './views/About.vue'),
        },
        {
          path: 'message',
          name: 'message',
          component: () => import(/* webpackChunkName: "about" */ './views/Message.vue'),
        },
        {
          path: '/sitemap',
          name: 'sitemap',
          // route level code-splitting
          // this generates a separate chunk (about.[hash].js) for this route
          // which is lazy-loaded when the route is visited.
          component: () => import(/* webpackChunkName: "about" */ './views/Sitemap.vue'),
        },
      ],
    },
    {
      path: '/login',
      name: 'login',
      component: () => import('./views/Login.vue'),
    },
    {
      path: '/management',
      name: 'management',
      component: () => import('./views/Management.vue'),
      async beforeEnter(to, from, next) {
        if (!store.state.loginUser.data) {
          await store.dispatch('loginUser/whoAmI');
        }

        if (store.state.loginUser.data) {
          // 有用户
          next();
        } else {
          next('/login');
        }
      },
      children: [
        {
          path: '/addblog',
          name: 'addblog',
          component: () => import('./views/AddBlog.vue'),
        },
        {
          path: '/addeveryday',
          name: 'addeveryday',
          component: () => import('./views/AddEveryDay.vue'),
        },
        {
          path: '/editblog',
          name: 'editblog',
          component: () => import('./views/EditBlog.vue'),
        },
        {
          path: '/artlist',
          name: 'artlist',
          component: () => import('./views/ArtList.vue'),
        },
      ],
    },
    {
      path: '*',
      name: '*',
      component: () => import('./views/404.vue'),
    },
  ],
});
