<template>
  <div class="leftbar">
    <div class="top">
      <!-- Random tag cloud -->
      随机标签云
    </div>
    <div class="tag-link">
      <tags-a v-for="item in tagList" :tagData="item" :key="item.tag" />
    </div>
  </div>
</template>

<script>
// <a v-for="item in tagList" :key="item.tag">{{item.tag}}</a>
import { getRandomTags } from '../../api';
import TagsA from './TagsA.vue';

export default {
  data() {
    return {
      tagList: Array,
    };
  },
  components: {
    TagsA,
  },
  methods: {
  },
  async created() {
    const result = await getRandomTags();
    if (+result.code === 0) {
      this.tagList = JSON.parse(result.data);
    }
  },
};
</script>

<style lang="scss" scoped>
.leftbar {
  /* width: 100%; */
  // height: 340px;
  background-color: #444;
  border-radius: 5px;
  margin-bottom: 20px;
  .top {
    height: 40px;
    line-height: 40px;
    background-color: #191818;
    color: #fff;
    padding: 0 15px;
    border-radius: 5px;
  }
  .tag-link {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    padding: 15px;

  }
}
</style>
