import Vue from 'vue';
import Vuex from 'vuex';
import loginUser from './loginUser';

Vue.use(Vuex);

export default new Vuex.Store({
  strict: true, // 生产模式关闭
  state: {
    artId: null,
    cId: { cid: null, isFocus: false },
    tagId: null,
    searchVal: null,
  },
  getters: {
    artId: state => state.artId,
  },
  mutations: {
    setArtId(state, { id }) {
      state.artId = id;
    },
    setCId(state, { cid }) {
      // console.log('setCId', cid);
      state.cId = { cid };
    },
    setTagId(state, { tid }) {
      state.tagId = tid;
    },
    setSearchVal(state, { searchVal }) {
      state.searchVal = searchVal;
    },
  },
  actions: {

  },
  // 登录模块
  modules: {
    loginUser,
  },
});
