export default {
  data() {
    return {
      searchVal: null,
    };
  },
  methods: {
    tz() {
      // if (this.searchVal) {
      this.$store.commit('setTagId', { tid: null });
      this.$store.commit('setSearchVal', { searchVal: this.searchVal });
      const currentPath = this.$router.history.current.path;
      if (currentPath.indexOf('main') < 0) {
        this.$router.push('/home/main');
      }
      // } else {
      //   this.$store.commit('setSearchVal', { searchVal: null });
      // }
    },
    resize() {
      // const cWidth = document.documentElement.clientWidth;
      // if (cWidth < 850) {
      //   this.show = false;
      // } else {
      //   this.show = true;
      // }
      // console.log('Header检测到resize事件!', document.documentElement.clientWidth);
    },
  },
  created() {
    // window.onresize = this.resize;
  },
  destroyed() {
    // window.onresize = null;
  },
};
</script>

<style lang="scss" scope>
a{
  cursor: pointer;
}
.logo {
  position: relative;
  display: inline-block;
  width: 240px;
  height: 56px;
  line-height: 56px;
  background-color: #ffad43;
  color: #fff;
//   border: 1px solid red;
  box-sizing: border-box;

  .site-branding-info {
    display: inline-block;
    width: 184px;
    height: 46px;
    line-height: 56px;
    margin-left: 56px;
    overflow: hidden;
    padding: 5px 0 5px 5px;
    .site-tit {
      height: 25px;
      line-height: 25px;
      font-size: 22px;
      margin: 3px 0;
    }
    .site-desc {
      height: 20px;
      line-height: 20px;
      font-size: 12px;
      margin: 0 0;
    }
  }
  &::before {
    content: "";
    position: absolute;
    width: 56px;
    height: 56px;
    left: 0px;
    background-color: #ff9000;
  }
}
.menu {
  display: inline-block;
  width: 300px;
  height: 56px;
  line-height: 56px;
  /* background-color: darkolivegreen; */
  box-sizing: border-box;
//   border: 1px solid red;
  font-size: 0;
  li {
    font-size: 16px;
    display: inline-block;
    width: 70px;
    height: 56px;
    overflow: hidden;
    padding: 0 12px;
    box-sizing: border-box;
    a {
      display: inline-block;
      width: 56px;
      height: 56px;
      text-align: center;
      color: #cdcdcd;
      text-decoration: none;
      &:hover {
        background-color: rgba(255, 255, 255, 0.2);
      }
    }
  }
}
.search {
  width: 230px;
  height: 56px;
  overflow: hidden;
  padding-left: 5px;
  padding-right: 5px;
  float: right;
  text-align: right;
  box-sizing: border-box;
  input {
    /* background: none; */
    outline: none;
    border: none;
    display: inline-block;
    width: 130px;
    height: 38px;
    margin: 0 0;
    border-radius: 2px;
    padding-left: 8px;
    margin: 8px;
    &:focus {
      box-shadow: inset 0px 0px 3px #00ffff;
    }
  }
  button {
    margin: 0;
    border: 0;
    outline: none;
    width: 50px;
    height: 38px;
    border-radius: 2px;
    &:hover {
      cursor: pointer;
    }
    &:active {
      background-color: #ccc;
       box-shadow: inset 0px 0px 3px #00ffff;
    }
  }
