<template>
  <div class="footer">
    <ul>
      <li v-for="item in tagList" :key="item.id">
        <a @click="tz(item.id)" >{{item.tag}}</a>
      </li>
    </ul>
   <p>王新龙个人博客</p>
  </div>
</template>

<script>
import { getAllTag } from '../api';
import { smoothScroll } from '../utile';

export default {
  data() {
    return {
      tagList: [],
    };
  },
  methods: {
    /** 跳转 */
    tz(tid) {
      this.$store.commit('setSearchVal', { cval: null });
      this.$store.commit('setTagId', { tid });
      const currentPath = this.$router.history.current.path;
      if (currentPath.indexOf('main') < 0) {
        this.$router.push('/home/main');
      }
      smoothScroll();
    },
  },
  async created() {
    const result = await getAllTag();
    if (+result.code === 0) {
      this.tagList = result.data;
    }
  },
};
</script>

<style lang="scss" scoped>
.footer {
  padding: 15px 15px;
  background-color: #444;
  height: 110px;
  ul {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 15px 15px;
    li {
      padding: 0 6px;
      a {
        font-size: 18px;
        /* font-weight: 600; */
        color: #c2c2c2;
        text-decoration: none;
        &:hover{
          cursor: pointer;
          color: #337ab7;
        }
      }
    }
  }
  p {
    margin-top: 30px;
    text-align: center;
    color: #c2c2c2;
  }
}
</style>
